body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #90DBF4;
  color: black;
  height: 100%;
  min-height: 100%;
  font-size: 1em;
}

nav {
  color: black;
  background-color: black;
  background-color: #0096c7;
  font-size: 2vmax;
  font-family: cursive, Geneva, Tahoma, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10vh;
  margin-bottom: 2vh;
}

.app--root {
  background-color: #90DBF4;
  /* background-color: black; */
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.main--root {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
}

input {
  height: 5vh;
  width: 99%;
  margin: 3px;
  border-radius: 5px;
  border: none;
  background-color: rgb(255, 255, 255);
  color: black;
  text-align: center;
  /* font-size: 1px; */
}

.formAndImage {
  display: flex;
  height: 50vh;
  width: 80vw;
  border-radius: 20px;
  padding: 2px;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 2px;
  background-color: #000d11;

  border-radius: 10px;
  background: #90DBF4;
  box-shadow:  05px 5px 10px #489dcf,
             -0px -0px 0px #62d5ff;
}
form {
  width: 30vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imageContainer {
  width: 40%;
  max-height: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  position: relative;
  color: rgb(233, 236, 236);
  font-size: 30px;
  text-align: center;

  text-shadow: 4px 4px 4px black;
}
.top, .bottom {
  position: absolute;
  top: 0.1em;
  /* font-size: 20px; */
  text-transform: uppercase;
  word-wrap: break-word;
}
.memeImage {
  max-width: 100%;
  max-height: inherit;
}
.imageDiv {
  max-height: inherit;
  border-radius: 5px;
  opacity: 0.6;
  transition: all 0.05s ease-out;
}
.imageDiv:hover {
  opacity: 1;
}

.memeListImage {
  max-height: 100%;
  max-width: auto;
  border-radius: 5px;
}

.memeListContainer {
  display: flex;
  align-self: center;
  justify-content: center;
  gap: 10px;
  height: 20vh;
  max-width: 100vw;
  overflow-x: scroll;
  overflow-y: hidden;
  display: flex;
  opacity: 0;
  transition: all 0.5s ease-in-out;
}

form > button {
  width: inherit;
  height: 2.5vmax;
  font-weight: bolder;
  margin-top: 3px;
  text-decoration-color: rgb(24, 41, 196);
  background-color: #B1E5D3;
  border-radius: 5px;
  font-size: 1vmax;
  transition: all 0.5s ease-in-out;
  font-family: cursive;
}
.randButton {
  width: 70%;
}
.textAlignButton {
  width: 90%;
}
.generateButton {
  width: inherit;
  height: 4vmax;
  font-size: 2vmax;
  background-color: black;
  animation-name: generate;
  animation-duration: 8s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
@keyframes  generate{
  0% {
    background-color: #b9fbc0;
  }
  25% {
    background-color: #90dbf4;
  }
  50% {
     background-color:  #ffcfd2;;
  }
  75% {
    background-color: #fbf8cc;
  }
  100% {
    background-color: #caf0f8;
  }
}
@keyframes backgroundColorPalette {
  0% {
    color: #b9fbc0;
  }
  25% {
    color: #90dbf4;
  }
  50% {
    color: #ffcfd2;
  }
  75% {
    color: #fbf8cc;
  }
  100% {
    color: #caf0f8;
  }
}
button:hover {
  background-color: rgb(128, 128, 128);
}
button:active {
  background-color: rgb(0, 0, 0);
  color: white;
}

.showMemes {
  color:rgb(42, 54, 165);
  background-color: #0096c7;
  /* text-decoration: underline; */
  font-size: 1.5vmax;
  /* margin-bottom: 0; */
  height: 5vh;
  animation-name: backgroundColorPalette;
  animation-duration: 8s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  
}

::-webkit-scrollbar {
  height: 0.7vh; 
}
::-webkit-scrollbar-track {
  background-color: blanchedalmond;
  border-radius: 20px;
}
::-webkit-scrollbar-thumb {
  background: rgb(105, 85, 85);
  border-radius: 20px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgb(0, 0, 0);
  border-radius: 0;
}
img {
  object-fit: cover !important;
}


@media only screen and (max-width: 840px) {
  .formAndImage {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    gap: 2vh;
    height: 60vh;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    width: 90vw;
    border-radius: 10px;
    background: #90DBF4;
    box-shadow:  5px 5px 10px #489dcf,
             -20px -20px 53px #62d5ff;
  }
  form {
    width: 70vw;
  }
  input {
    height: 3vh;
    width: inherit;
    font-size: 26px;
  }
  button {
    height: 3vh;
    width:  inherit;
  }
  
  .imageContainer {
    min-width: 80vw;
    min-height:30vh;
    max-height: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    position: relative;
    color: rgb(233, 236, 236);
    font-size: 0.9em;
    text-align: center;
    text-shadow: 4px 4px 4px black;
  }

  form > button {
    width: inherit;
    height: 3.5vmax;
    margin-top: 10px;
    border-radius: 5px;
    font-size: 1.9vmax;
    font-family: sans-serif;
  }
  .randButton {
    width: 70%;
  }
  .textAlignButton {
    width: 90%;
  }
  .generateButton {
    width: inherit;
    height: 4vmax;
    font-size: 2vmax;
    background-color: black;
    animation-name: generate;
    animation-duration: 8s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
  }
  .top, .bottom {
    font-size: 13px;
  }
  input {
    font-size: 10px;
  }
}